import React from 'react';
import { graphql } from 'gatsby';
import { useIntl, FormattedMessage, Link } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Query } from '../../graphql-types';
import { Separator } from '../components/separator';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Breadcrumb from 'react-bootstrap/Breadcrumb';

const NewsItemPage: React.FunctionComponent<{ data: Query }> = ({
  data: {
    allMarkdownRemark: { nodes },
  },
}) => {
  const intl = useIntl();
  const localised = nodes.filter((v) => v.fields?.lang === intl.locale);
  const defaultLocale = nodes.filter((v) => v.fields?.lang === intl.defaultLocale);
  const {
    frontmatter: { title, description, image },
    html,
  } = localised?.[0] ?? defaultLocale?.[0] ?? nodes?.[0];

  return (
    <Layout>
      <SEO title={title} description={description} image={image.childImageSharp?.original?.src} />
      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <Breadcrumb>
                    <Breadcrumb.Item>
                      <Link to="/">
                        <FormattedMessage id="home" />
                      </Link>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item active>{title}</Breadcrumb.Item>
                  </Breadcrumb>
                  <h1 className="page-title">{title}</h1>
                  <p className="page-description">{description}</p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>

          <Container dangerouslySetInnerHTML={{ __html: html }} />
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    allMarkdownRemark(filter: { fields: { slug: { eq: $slug } } }) {
      nodes {
        frontmatter {
          description
          title
          image {
            childImageSharp {
              original {
                src
              }
            }
          }
        }
        fields {
          content
          lang
          slug
        }
        html
      }
    }
  }
`;

export default NewsItemPage;
